*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body{
  font-family:  -apple-system, BlinkMacSystemFont, 
                'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 
                'Open Sans', 'Helvetica Neue', sans-serif;
  line-height: 1.5;
}
.main{
  margin: 2rem auto;
  width: 50%;
}
.main_title{
  margin:2rem 0 0;
  text-align: center;
  font-size: 2.5rem;
}
.underline{
  border: 2px solid #4D77FF;
  width: 10%;
  margin: 0 auto 2rem;
}
.main-div{
  border-radius: 5px;
  background-color: #fff;
  box-shadow: 1px 1px 10px 1px grey;
  margin: 2rem 0
}
.main-img{
  width:100%;
  max-height: 350px;
  object-fit: cover;
}
.main_contents{
  padding: 1rem
}
.h2-flex{
  display: flex;
  margin:1rem 0;
  font-size: .85rem
}
.price{
  margin-left: auto;
  color: #4D77FF;
  background-color: #EFFFFD;
  padding: .2rem 1rem;
}
.more, .remove{
  display:block;
  font-size: 1rem;
  cursor: pointer;
}
.more{
  margin: 1rem 0;
  color: #4D77FF;
  border: none;
  background-color: transparent;
  text-transform: capitalize;
}
.remove{
  color: #890F0D;
  border: 1px solid #890F0D;
  background-color: #fff;
  padding: .5rem 2rem;
  margin: 1rem auto;
}
@media(max-width: 800px){
  .main{
    width: 75%;
  }
}
@media(max-width: 580px){
  .h2-flex{
    align-items: baseline;
  }
  .tour_title{
    width:70%
  }
}
@media(max-width: 425px){
  .main{
    width: 90%;
  }
  .tour_title{
    width:60%
  }
  .h2-flex{
   font-size: 10px;
  }
}